// carousel.less
.carousel-fade .carousel-inner .item {
  opacity: 0;
  transition-property: opacity;
}

.carousel-fade .carousel-inner .active {
  opacity: 1;
}

.carousel-fade .carousel-inner .active.left,
.carousel-fade .carousel-inner .active.right {
  left: 0;
  opacity: 0;
  z-index: 1;
}

.carousel-fade .carousel-inner .next.left,
.carousel-fade .carousel-inner .prev.right {
  opacity: 1;
}

.carousel-fade .carousel-control {
  z-index: 2;
}

@media all and (transform-3d), (-webkit-transform-3d) {
  .carousel-fade .carousel-inner > .item.next,
  .carousel-fade .carousel-inner > .item.active.right {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  .carousel-fade .carousel-inner > .item.prev,
  .carousel-fade .carousel-inner > .item.active.left {
    opacity: 0;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
  .carousel-fade .carousel-inner > .item.next.left,
  .carousel-fade .carousel-inner > .item.prev.right,
  .carousel-fade .carousel-inner > .item.active {
    opacity: 1;
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
  }
}

.carousel-caption {
  left: 0;
  right: 0;
  bottom: 0;
  padding: 10px 15px 5px;
  #gradient.horizontal(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0), 0, 80%);

  @media screen and (min-width: @screen-sm-min) {
    left: 0;
    right: 0;
    padding-bottom: 5px;
  }
}

// jumbotron.less
.jumbotron {
  padding: @jumbotron-padding;

  p {
    line-height: (@jumbotron-font-size * 2);
    font-weight: normal;
  }

  @media screen and (min-width: @screen-sm-min) {
    padding: (@jumbotron-padding) 0;

    .container &,
    .container-fluid & {
      padding-left:  (@jumbotron-padding);
      padding-right: (@jumbotron-padding);
    }

    h1,
    .h1 {
      font-size: (@font-size-base * 2);
    }
  }
}

// navbar.less
.navbar-nav {
  > li > a {
    font-weight: 600;
  }
}

// navs.less
.nav-tabs {
  > li > a {
    font-size: @font-size-large;
  }
}

// scaffolding.less
body {
  padding-top: 60px;
}

figure {
  margin: 21px 40px 10.5px;
  text-align: center;

  img {
    display: inline-block;
    border-radius: 4px;
    box-shadow: 4px 4px 4px rgba(90,90,90,0.5);
  }
}

img {
  .img-responsive(); // force all images to be responsive
}

.thumbnails {
  display: table;
  list-style: none;
  padding: 0;
  li {
    width: 49%;
    float: left;
  }
}

// type.less
p {
  text-align: justify;
}

footer p {
    text-align: center;
}

// wells.less
@media (min-width: 992px) {
  .col-md-4.well {
    width: 30%;
    margin-left: 15px;
  }
}
